.port
  background-color: $bg
  display: flex
  flex-direction: column
  align-items: center
  padding-top: 40px
  padding-bottom: 120px

  &__inner
    max-width: 1200px
    padding-left: 30px
    padding-right: 30px
    display: flex
    flex-direction: column
    align-items: center

  &__project
    display: flex
    flex-direction: column
    align-items: center
    margin-bottom: 90px

  &__h2
    font-family: $helb
    text-align: center
    color: $text
    font-size: $h2
    line-height: 1
    letter-spacing: 1.28px
    margin: 0
    margin-bottom: 28px
    max-width: 940px

  &__h3
    font-family: $hel
    font-weight: 400
    text-align: center
    color: $text
    font-size: $h3
    line-height: 1
    letter-spacing: 1.28px
    margin: 0
    margin-bottom: 28px
    max-width: 940px

  &__p
    font-family: $tit
    text-align: center
    color: $text-p
    font-size: 21px
    line-height: 1.25
    letter-spacing: 0.4px
    margin: 0
    margin-bottom: 18px
    max-width: 760px


@media (max-width: 1200px)
  .port
    &__h2
      font-size: $h2-1200

    &__h3
      font-size: $h3-1200


@media (max-width: 990px)
  .port
    &__h2
      font-size: $h2-990

    &__h3
      font-size: $h3-990


@media (max-width: 768px)
  .port
    padding-bottom: 70px
    
    &__h2
      font-size: $h2-768

    &__h3
      font-size: $h3-768


@media (max-width: 480px)
  .port
    &__inner
      padding-left: 15px
      padding-right: 15px
      
    &__h2
      font-size: $h2-480

    &__h3
      font-size: $h3-480