.hero
  background-color: $bg
  display: flex
  flex-direction: column
  align-items: center
  padding-top: 170px
  padding-bottom: 0

  &__inner
    max-width: 1200px
    padding-left: 30px
    padding-right: 30px
    display: flex
    flex-direction: column
    align-items: center

  &__h1
    font-family: $helb
    text-align: center
    color: $text
    font-size: $h1
    line-height: 1.2
    letter-spacing: 1.28px
    margin: 0
    margin-bottom: 32px


  &__h2
    font-family: $helt
    font-weight: 400
    text-align: center
    color: $text
    font-size: $h2-hero
    line-height: 1
    letter-spacing: 1.28px
    margin: 0
    margin-bottom: 28px
    max-width: 940px

  &__p
    font-family: $tit
    text-align: center
    color: $text-p
    font-size: $hero-p
    line-height: 1.25
    letter-spacing: 0.4px
    margin: 0
    margin-bottom: 40px
    max-width: 760px

  &__caption
    margin-top: 42px
    margin-bottom: -6px
    background-color: #EFEEEE
    padding: 24px
    border-radius: 24px 24px 0 0
    font-family: $hel
    font-size: 32px
    font-weight: 400
    color: $text
    text-align: center
    max-width: 60%
    line-height: 1.25

  &__map
    max-width: 960px
    width: 100%
    border-radius: 16px


@media (max-width: 1200px)
  .hero
    
    &__h1
      font-size: $h1-1200
      margin-bottom: 21px

    &__h2
      font-size: $h2-hero-1200

    &__p
      font-size: $hero-p-1200

    &__caption
      max-width: 80%
      font-size: 28px



@media (max-width: 990px)
  .hero
    padding-top: 150px
    
    &__h1
      font-size: $h1-990

    &__h2
      font-size: $h2-hero-990
      letter-spacing: unset

    &__p
      font-size: $hero-p-990

    &__caption
      font-size: 26px
      margin-top: 32px



@media (max-width: 768px)
  .hero
    
    &__h1
      font-size: $h1-768
      margin-bottom: 12px

    &__h2
      font-size: $h2-hero-768
      line-height: 1.35
      max-width: 460px
      margin-bottom: 18px

    &__p
      font-size: $hero-p-768
      max-width: 460px

    &__caption
      font-size: 21px
      margin-top: 24px

    &__map
      border-radius: 8px
      

  @media (max-width: 480px)
    .hero
      padding-top: 130px

      &__inner
        padding-left: 15px
        padding-right: 15px

      &__h1
        font-size: $h1-480

      &__caption
        font-size: 21px
        margin-top: 30px