.table
  display: flex
  flex-direction: column
  gap: -1px
  width: 100%

  &__row
    display: flex
    flex-direction: row
    gap: -1px
    width: 100%
    transition: all 0.3s ease
    box-shadow: rgba(0, 0, 0, 0) 0px 8px 36px
    text-decoration: none

    &:hover
      @media #{$mouse-device}
        cursor: pointer
        box-shadow: rgba(0, 0, 0, 0.25) 0px 8px 36px

    &:active
      cursor: pointer
      box-shadow: rgba(0, 0, 0, 0.25) 0px 8px 36px
    

  &__cell
    display: flex
    justify-content: center
    align-items: center
    padding: 10px 16px
    border: 1px solid $border
    width: 100%

  &__logo-wrapper
    width: 100%
    max-width: 132px

  &__logo
    border-radius: 50%

  &__name-wrapper
    max-width: 160px
    justify-content: flex-start

  &__name
    font-family: $helb
    font-weight: 700
    color: $text
    font-size: 22px
    text-align: left

  &__description
    font-family: $tit
    font-weight: 400
    color: $text
    font-size: 16px

  &__links-wrapper
    max-width: 150px

  &__links
    display: flex
    flex-direction: column
    gap: 6px
    list-style-type: none
    padding-left: 0
    margin: 0
    width: 100%

  &__link
    text-decoration: none
    font-family: $tit
    font-weight: 700
    color: $text
    font-size: 16px
    transition: all 0.3s ease

    &:hover
      @media #{$mouse-device}
        color: $text-hover

    &:active
      color: $text-hover


@media (max-width: 990px)
  .table
    &__logo-wrapper
      display: none


@media (max-width: 768px)
  .table
    &__name-wrapper
      max-width: 100px
      

    &__name
      font-size: 21px

@media (max-width: 480px)
  .table
    &__description-wrapper
      display: none

    &__name-wrapper
      max-width: 100%