.header
  width: 100%
  height: 80px
  display: flex
  justify-content: center
  align-items: center
  background-color: $bg
  box-shadow: 0 1px 24px 0 rgba(17, 17, 17, 0.09)
  position: fixed
  top: 0
  left: 0
  z-index: 10

  &__inner
    max-width: 1200px
    width: 100%
    display: flex
    justify-content: space-between
    align-items: center
    padding-left: 30px
    padding-right: 30px
    box-sizing: content-box

  &__blur,
  &__mobile
    display: none

  &__links
    display: flex
    gap: 22px
    list-style-type: none

  &__link
    position: relative
    font-family: $tit
    font-weight: 600

    color: $text
    font-size: 18px
    text-decoration: none
    transition: all 0.3s ease-in-out

    &::before
      content: ""
      position: absolute
      bottom: 0
      left: 0
      display: block
      width: 100%
      height: 2px
      background-color: $text-hover
      transform: scaleX(0)
      transition: all 0.3s ease-in-out

    &:hover
      color: $text-hover

      &::before
        transform: scaleX(1)
    
  &__close-menu
    position: absolute
    top: 20px
    right: 20px
    z-index: 99999
    background-image: url('img/icon--menu-close.svg')
    width: 40px
    height: 32px

  &__open-menu
    background-image: url('img/icon--menu-open.svg')
    width: 40px
    height: 32px


  &__close-menu,
  &__open-menu
    border: none
    background-size: 100%
    background-repeat: no-repeat
    background-color: transparent
    



@media (max-width: 768px)
  .header
    &__logo
      max-width: 50px


    &__link
      font-size: 16px


@media (max-width: 480px)
  .header
    &__inner
      padding-left: 15px
      padding-right: 15px

    &__mobile
      display: block
      z-index: 99999

    &__links
      position: absolute
      top: 0
      left: 0
      background-color: $bg
      flex-direction: column
      padding-left: 0
      align-items: center
      width: 100%
      margin-top: 0
      gap: 0
      display: none
      z-index: 99999

    &__link-wrapper
      display: flex
      justify-content: center
      align-items: center
      width: 100%

    &__link
      display: flex
      justify-content: center
      align-items: center
      width: 100%
      height: 120px
      font-size: 30px
      text-align: center

      &:hover
        color: $text

      &::before
        display: none

    &__blur
      position: absolute
      width: 100vw
      height: 100vh
      backdrop-filter: blur(15px)
      background-color: rgba(0,0,0,0.1)
      top: 0
      left: 0
      z-index: 9
      display: none

  .opened .header__links
    display: flex

  .opened .header__blur
    display: block
      